*{
    margin: 0;
    padding: 0;
}
.main{
    display: flex;
    flex-wrap: wrap;
    height: 80vh;
}
header{
    width: 100%;
    height: 20vh;
    background-image: url("https://hdwallpaperim.com/wp-content/uploads/2017/08/22/347015-Veil_Nebula-space-nebula-NASA-science-stars-universe.jpg");
    background-size: cover;
    background-position: center;
}
.navbar{
    height: 5vh;
}
section{
    display: flex;
    flex-wrap: wrap;
    height: 70vh;
    width: 100%;
}
.left, .right{
    width: 25%;
    height: 60vh;
    display: flex;
    flex-wrap: wrap;
}
center{
    width: 50%;
    height: 60vh;
}
.box{
    width: 100%;
    height: 10vh;
    display: flex;
    flex-wrap: wrap;
    padding: 5px;
    align-items: center;
    justify-content: center;
}
.text{
    width: 70%;
    height: 100%;
    padding: 5px;
    text-align: start;
}
h6{
    font-weight: bold;
}
.box p{
    text-align: start;
}
center .image{
    width: 100%;
    height: 75%;
    position: relative;
    overflow: hidden;
    padding: 10px;
}
.image img{
    position: absolute;
    width: 100%;
    left: 0;
}
.center-text{
    text-align: start;
    width: 100%;
}
.center-text h1{
    width: 100%;
}
.center-text p{
    width: 100%;
}